import axios from 'axios';
import React, { Component, useState } from 'react';
import { connect } from 'react-redux'
import { cartDataSet } from '../Redux/Actions/cartAction'
import '../ProductDetails/productdetails.css'
export class Details extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ProductDetail: [],
            idp: props.match.params.idp,

        }

    }
    componentDidMount() {
        axios.get("https://5d76bf96515d1a0014085cf9.mockapi.io/product/" + this.state.idp)
            .then((res) => {
                console.log(res.data)
                this.setState({ ProductDetail: res.data })
            })
    }
    render() {
        return (
            <section className="mysection">
                <div>
                    <img src={this.state.ProductDetail.preview} className="mainP" />
                </div>
                <div>
                    <div>
                        <h1>{this.state.ProductDetail.name}</h1>
                        <h4>{this.state.ProductDetail.brand}</h4>
                        <h3><span>Price: Rs </span><span>{this.state.ProductDetail.price}</span></h3>
                        <div>
                            <h3>Description</h3>
                            <p>{this.state.ProductDetail.description}</p>
                        </div>
                        <div>
                            <h3>Product Preview</h3>
                            <div>
                                {this.state.ProductDetail.photos !== undefined && 
                                this.state.ProductDetail.photos.map((a,index) => <SmallImg a={a} index={index} />)}

                            </div>
                        </div>
                    </div>
                    <button id="btn-add-to-cart" onClick={() =>
                         { this.props.kei(this.state.ProductDetail) }}>Add to Cart</button>
                </div>
            </section>

        )
    }

}
const mapDispatchToProps = (Dispatch) => ({
    kei: (payload) => Dispatch(cartDataSet(payload))
})

export default connect(null, mapDispatchToProps)(Details)


export const SmallImg = (props) => {
    const handelBorder = (z,Sourc)=> 
    {
         let Imagewala = document.getElementsByClassName("mainP")
         Imagewala[0].setAttribute("src",Sourc)



        let saareElement = document.getElementsByClassName("borderImage")
        console.log(saareElement)

        for (let i=0; i<saareElement.length; i++){
            if(i==z){
                saareElement[i].classList.add("borderBox")
            }
            else{
                saareElement[i].classList.remove("borderBox")
            }
        }
    }
    const {index,a}=props

    return (
        <div id={index}
             className="borderImage"
             onClick={(e) => {handelBorder(index,a)
            console.log(index)
            }}
         >
             <img src={a} className="smallImg"></img>
        </div>
    );
}


