import "../src/App.css";
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { CardFillerCloth, CardFillerAccessories } from "./Components/Card/index"
import { productDataSet } from './Components/Redux/Actions/productDataAction/productDataAction'
import axios from 'axios'
import { HashRouter, Route, Switch, Link } from 'react-router-dom'
import Details from './Components/ProductDetails'
import Topbar from "./Components/Topbar/index"
import Cart from "./Components/Cart";
import Checkout from "./Components/Checkout";

export class App extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    axios.get("https://5d76bf96515d1a0014085cf9.mockapi.io/product")
      .then((res) => {
        console.log("axiosData", res.data)
        this.props.kei(res.data)
      })

  }
  render() {
    return (

      <HashRouter>
        <Topbar />
        <Switch>
          <Route path="/productdetail/:idp" component={Details}></Route>
          <Route path="/cart" component={Cart}></Route>
          <Route path="/checkout" component={Checkout}></Route>
          <div className="maindiv">
            {console.log("redadta", this.props.DataCard)}
            <section className="ClothingSection">
              <h2>Clothing for Men and Women</h2>
              <div className="cardContainer1">
                {this.props.DataCard.length === 0 ? "" : this.props.DataCard[0].map(items => <CardFillerCloth {...items} />)}
              </div>
            </section>

            <section className="AssessorySection">
              <h2>Accessories for Men and Women</h2>
              <div className="cardContainer1">
                {this.props.DataCard.length === 0 ? "" : this.props.DataCard[0].map(items => <CardFillerAccessories {...items} />)}
              </div>
            </section>
          </div>
        </Switch>
        {/* footer */}
      </HashRouter>

    )
  }
}

const mapStateToProps = (state) => ({
  DataCard: state.productData.ProductDataArray

})

const mapDispatchToProps = (Dispatch) => ({
  kei: (payload) => Dispatch(productDataSet(payload))
})

export default connect(mapStateToProps, mapDispatchToProps)(App)
