const initialState = {
   CartDataArray:[]
    }
    
    function CartData (state = initialState, { type, payload })  {
        switch (type) {
    
        case "cart":{
            console.log(state)
            return { ...state,CartDataArray:[...state.CartDataArray,payload]}
        }
            
    
        default:
            return state
        }
    }
    export default CartData
    