import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import "../Card/card.css"
export const CardFillerCloth = ({ id, preview, name, brand, description, isAccessory, price }) => {
  const linky = "/productdetail" + "/" + id
  return (
    <div>
      <Link to={linky}  style={{ textDecoration: 'none' }}>
        {isAccessory === false ?
          (<div className="card1" id={id}>
            <div className="imgContainer1">
              <img src={preview} />
            </div>
            <div className="detailDiv">
              <h3>{name}</h3>
              <h4>{brand}</h4>
              <h5>{price}</h5>
            </div>
          </div>)
          : ""}
      </Link>
    </div>
  );

}

export const CardFillerAccessories = ({ id, preview, name, brand, price, description, isAccessory }) => {
  const linky = "/productdetail" + "/" + id
  return (
    <div>
      <Link to={linky}  style={{ textDecoration: 'none' }}>
      {isAccessory === true ?
        (<div className="card1" id={id}>
          <div className="imgContainer1">
            <img src={preview} />
          </div>
          <div className="detailDiv">
            <h3>{name}</h3>
            <h4>{brand}</h4>
            <h5>{price}</h5></div>
            </div>)
        : ""}
        </Link>
    </div>
  );
}